body{
    background-color: #ccc;
}
.container, .bodys {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    height: 50vh;
    padding: 0 20px;
    box-sizing: border-box;
  }
  .searchinput {
    width: 100%;
    max-width: 600px;
    padding: 10px;
    font-size: 16px;
    border: 1px solid #ccc;
    border-radius: 4px;
    margin-bottom: 10px;
  }
  .searchbtn {
    width: 100%;
    max-width: 600px;
    padding: 10px;
    font-size: 16px;
    border: none;
    border-radius: 4px;
    background-color: green;
    color: white;
    cursor: pointer;
  }
  .searchbtn:hover {
    background-color: #0056b3;
  }
  body {
    font-family: Arial, sans-serif;
    margin: 0;
    padding: 0;
}
.fetchdata {
    white-space: pre-wrap; 
    word-wrap: break-word; 
    background-color:rgb(134, 134, 134);
    color: white;
    padding: 10px;
    border: 1px solid #ccc;
    border-radius: 5px;
    max-width: 100%;
    box-sizing: border-box; 
    overflow-x: auto;
    align-items: center;
    justify-content: center;
    text-decoration: none;
}
